}

button {
    border: none;
    background: var(--main-blue);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: relative;
    color: white;
    border-radius: 3px;
    padding: 12px 22px;
    cursor: pointer;
    transition: all 0.35s;
    font-family: 'Roboto', sans-serif;
    font-size: 0.8125rem;
    text-transform: uppercase;
    box-shadow:
        0 0px 1px rgba(54, 101, 222, 0.2), 
        0 1px 2px rgba(54, 101, 222, 0.2), 
        0 2px 4px rgba(54, 101, 222, 0.2), 
        0 3px 8px rgba(54, 101, 222, 0.2);
  }
  
  button:hover {
    background:#2c53b4;
  }
  
  button:disabled {
    background: #dbdfe4;
    color: #f0f2f5;
    box-shadow: none;
    cursor: progress;
  }
